import React, { useState } from 'react'
import {
  Typography,
  TextField,
  Container,
  Box,
  List,
  ListItem,
  ListItemText,
  IconButton,
} from '@material-ui/core'
import { Add } from '@material-ui/icons'
import truncate from 'lodash/truncate'

function AsyncWordList() {
  const [error, setError] = useState('')
  const [words, setWords] = useState([])
  const [capturedWords, setCapturedWords] = useState([])
  const [currentWord, setCurrentWord] = useState('')

  const changeInput = (event) => {
    setCurrentWord(event.target.value)
  }

  const checkEnter = (event) => {
    if (event.key === 'Enter') addWord()
  }

  const addWord = () => {
    if (words.includes(currentWord))
      return setError('has already included the word')
    if (currentWord.trim() === '') return setError('empty input is not allowed')

    setError('')
    setCurrentWord('')
    setCapturedWords([currentWord, ...words])
    setTimeout(() => {
      setWords([currentWord, ...capturedWords])
    }, 500)
  }

  return (
    <>
      <Typography component="h1" variant="h4" align="center">
        AsyncWordList
      </Typography>
      <Box marginTop={3} marginBottom={3}>
        <Container maxWidth="sm">
          <Box display="flex" alignItems="center">
            <TextField
              variant="outlined"
              label="Word"
              placeholder="Enter new word..."
              name="word"
              inputProps={{ 'data-testid': 'my-word-input' }}
              fullWidth
              value={currentWord}
              onChange={changeInput}
              onKeyPress={checkEnter}
              helperText={<span data-testid="word-input-error">{error}</span>}
              error={Boolean(error)}
            ></TextField>
            <IconButton
              type="submit"
              data-testid="add-word-button"
              onClick={addWord}
            >
              <Add></Add>
            </IconButton>
          </Box>
          <List data-testid="word-list">
            {words.map((word, index) => (
              <ListItem key={word} data-testid={`word-list-item-${index + 1}`}>
                <ListItemText primary={truncate(word, { length: 15 })} />
              </ListItem>
            ))}
          </List>
        </Container>
      </Box>
    </>
  )
}

export default AsyncWordList
