import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Route, Redirect } from 'react-router-dom'

import * as uiActions from 'modules/ui/actions'

export default function ProtectedRoute({ children, roles, ...rest }) {
  const dispatch = useDispatch()
  const { isLoading, isProfileLoaded, user } = useSelector(
    (state) => state.auth
  )

  return (
    <Route
      {...rest}
      render={() => {
        if (!isProfileLoaded) return null
        if (isLoading) return null
        if (!user || !roles.includes(user.role)) {
          dispatch(
            uiActions.setFlashMessage('You are not allowed to access this page')
          )
          return <Redirect to="/" />
        }

        return children
      }}
    ></Route>
  )
}
