import React, { useEffect } from 'react'
import {
  Container,
  Typography,
  TextField,
  Box,
  Select,
  Button,
  FormControl,
  InputLabel,
  FormHelperText,
  FormLabel,
  FormControlLabel,
  RadioGroup,
  Radio,
} from '@material-ui/core'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers'
import * as yup from 'yup'
import axios from 'lib/axios'

function PostForm() {
  const {
    register,
    handleSubmit,
    errors,
    setValue,
    formState: { isDirty, isValid },
  } = useForm({
    mode: 'onChange',
    resolver: yupResolver(
      yup.object().shape({
        title: yup.string().required(),
        category: yup.string().oneOf(['java', 'python', 'ruby']).required(),
        status: yup.string().oneOf(['drafted', 'published']).required(),
        content: yup.string().required(),
      })
    ),
  })

  const onSubmit = async (post) => {
    await axios.post('/posts', post)
  }

  useEffect(() => {
    register('status')
  }, [register])

  return (
    <Container maxWidth="sm">
      <Box marginBottom={2}>
        <Typography variant="h4" component="h1" align="center">
          Post Form
        </Typography>
      </Box>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Box display="flex" flexDirection="column">
          <TextField
            inputRef={register}
            variant="outlined"
            label="Title"
            placeholder="Enter a title"
            name="title"
            inputProps={{ 'data-testid': 'post-form-title' }}
            fullWidth
            helperText={
              <span data-testid="post-form-title-helper">
                {errors.title?.message || ''}
              </span>
            }
            error={Boolean(errors.title)}
          ></TextField>
          <FormControl error={Boolean(errors.category)}>
            <InputLabel htmlFor="post-form-category">Category</InputLabel>
            <Select
              native
              name="category"
              inputRef={register}
              inputProps={{
                id: 'post-form-category',
                'data-testid': 'post-form-category',
              }}
            >
              <option aria-label="None" value="" />
              <option value="java">Java</option>
              <option value="python">Python</option>
              <option value="ruby">Ruby</option>
            </Select>
            <FormHelperText>{errors.category?.message || ''}</FormHelperText>
          </FormControl>
          <Box marginTop={2}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Status</FormLabel>
              <RadioGroup
                native
                aria-label="status"
                name="status"
                onChange={(e) => setValue('status', e.target.value)}
              >
                <FormControlLabel
                  value="drafted"
                  control={
                    <Radio
                      inputProps={{
                        'data-testid': 'post-form-drafted-status',
                      }}
                    />
                  }
                  label="Drafted"
                />
                <FormControlLabel
                  value="published"
                  control={
                    <Radio
                      inputProps={{
                        'data-testid': 'post-form-published-status',
                      }}
                    />
                  }
                  label="Published"
                />
              </RadioGroup>
            </FormControl>
          </Box>
          <TextField
            inputRef={register}
            multiline
            rows={10}
            variant="outlined"
            label="Content"
            placeholder="Enter a content"
            name="content"
            inputProps={{ 'data-testid': 'post-form-content' }}
            fullWidth
            helperText={
              <span data-testid="post-form-content-helper">
                {errors.content?.message || ''}
              </span>
            }
            error={Boolean(errors.content)}
          ></TextField>
          <Box marginTop={2} marginLeft="auto">
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={!isValid || !isDirty}
              data-testid="post-form-submit-button"
            >
              Submit
            </Button>
          </Box>
        </Box>
      </form>
    </Container>
  )
}

export default PostForm
