import React from 'react'
import {
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Container,
  Box,
  Typography,
} from '@material-ui/core'

const members = [
  { id: 1, name: 'Sathit', avatar: require('./images/users/sathit.png') },
  { id: 2, name: 'Sombut', avatar: require('./images/users/sombut.png') },
  { id: 3, name: 'Somchai', avatar: require('./images/users/somchai.png') },
  {
    id: 4,
    name: 'Somprasong',
    avatar: require('./images/users/somprasong.png'),
  },
  { id: 5, name: 'Sornthep', avatar: require('./images/users/sornthep.png') },
]

function MemberList() {
  return (
    <Container maxWidth="sm">
      <Box marginBottom={3}>
        <Typography align="center" variant="h4" component="h1">
          Member List
        </Typography>
      </Box>
      <List data-testid="member-list">
        {members.map((member) => (
          <ListItem key={member.id} data-testid="member-item">
            <ListItemAvatar>
              <Avatar
                alt={member.name}
                src={member.avatar}
                imgProps={{ 'data-testid': 'member-avatar' }}
              ></Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={member.name}
              data-testid="member-name"
            ></ListItemText>
          </ListItem>
        ))}
      </List>
    </Container>
  )
}

export default MemberList
