import React, { useState, useEffect, useCallback } from 'react'
import { useDebouncedCallback } from 'use-debounce'
import axios from 'lib/axios'
import {
  Typography,
  TextField,
  Container,
  Box,
  List,
  ListItem,
  ListItemText,
  IconButton,
  ButtonGroup,
} from '@material-ui/core'
import { Add, Edit, Delete, Save } from '@material-ui/icons'
import truncate from 'lodash/truncate'

function WordListAPI() {
  const [error, setError] = useState('')
  const [editError, setEditError] = useState('')
  const [words, setWords] = useState([])
  const [currentWord, setCurrentWord] = useState('')
  const [editingWord, setEditingWord] = useState('')
  const [currentEditingId, setCurrentEditingId] = useState(0)
  const [isEditing, setIsEditing] = useState(false)

  const changeInput = (event) => {
    setCurrentWord(event.target.value)
  }

  const checkEnter = (event) => {
    if (event.key === 'Enter') addWord()
  }

  const changeEditInput = (event) => {
    setEditingWord(event.target.value)
  }

  const checkEditEnter = (event) => {
    if (event.key === 'Enter') updateWord()
  }

  const updateWord = async (id) => {
    if (editingWord.trim() === '')
      return setEditError('empty input is not allowed')

    const {
      data: { word },
    } = await axios.patch(`/words/${id}`, { word: editingWord })

    setEditError('')
    setEditingWord('')
    setIsEditing(false)
    setCurrentEditingId(0)
    setWords(words.map((w) => (w.id === id ? word : w)))
  }

  const addWord = async () => {
    if (words.includes(currentWord))
      return setError('has already included the word')
    if (currentWord.trim() === '') return setError('empty input is not allowed')

    setError('')
    setCurrentWord('')

    const {
      data: { word },
    } = await axios.post('/words', { word: currentWord })

    setWords([word, ...words])
  }

  const editWord = (id) => {
    if (isEditing) {
      if (currentEditingId === id) return updateWord(id)

      setCurrentEditingId(id)
      setEditingWord(words.find((w) => w.id === id).word)
      setIsEditing(true)
      return
    }

    setCurrentEditingId(id)
    setEditingWord(words.find((w) => w.id === id).word)
    setIsEditing(!isEditing)
  }

  const deleteWord = async (id) => {
    await axios.delete(`/words/${id}`)

    setWords(words.filter((w) => w.id !== id))
  }

  const fetchWords = useCallback(async () => {
    const {
      data: { words },
    } = await axios.get('/words')

    setWords(words)
  }, [])

  const [search] = useDebouncedCallback(async (word) => {
    const {
      data: { words },
    } = await axios.get(`/words?term=${word}`)

    setWords(words)
  }, 1000)

  useEffect(() => {
    fetchWords()
  }, [fetchWords])

  return (
    <>
      <Typography component="h1" variant="h4" align="center">
        WordList API
      </Typography>
      <Box marginTop={3} marginBottom={3}>
        <Container maxWidth="sm">
          <Box display="flex" alignItems="center">
            <TextField
              variant="outlined"
              label="Search"
              placeholder="Enter a word..."
              name="search"
              inputProps={{ 'data-testid': 'search-input' }}
              fullWidth
              onChange={(e) => search(e.target.value)}
            ></TextField>
            <TextField
              variant="outlined"
              label="Word"
              placeholder="Enter new word..."
              name="word"
              inputProps={{ 'data-testid': 'my-word-input' }}
              fullWidth
              value={currentWord}
              onChange={changeInput}
              onKeyPress={checkEnter}
              helperText={<span data-testid="word-input-error">{error}</span>}
              error={Boolean(error)}
            ></TextField>
            <IconButton
              type="submit"
              data-testid="add-word-button"
              onClick={addWord}
            >
              <Add></Add>
            </IconButton>
          </Box>
          <List data-testid="word-list">
            {words.map(({ word, id }) => (
              <ListItem key={word} data-testid={`word-list-item-${id}`}>
                {isEditing && currentEditingId === id ? (
                  <TextField
                    variant="outlined"
                    label="Word"
                    placeholder="Enter updated word..."
                    name="word"
                    inputProps={{ 'data-testid': 'edit-word-input' }}
                    fullWidth
                    value={editingWord}
                    onChange={changeEditInput}
                    onKeyPress={checkEditEnter}
                    helperText={
                      <span data-testid="edit-input-error">{editError}</span>
                    }
                    error={Boolean(editError)}
                  ></TextField>
                ) : (
                  <ListItemText primary={truncate(word, { length: 15 })} />
                )}

                <ButtonGroup variant="contained" color="primary">
                  <IconButton
                    type="submit"
                    data-testid="edit-word-button"
                    onClick={() => editWord(id)}
                  >
                    {isEditing && currentEditingId === id ? (
                      <Save></Save>
                    ) : (
                      <Edit></Edit>
                    )}
                  </IconButton>
                  <IconButton
                    type="submit"
                    data-testid="delete-word-button"
                    onClick={() => deleteWord(id)}
                  >
                    <Delete></Delete>
                  </IconButton>
                </ButtonGroup>
              </ListItem>
            ))}
          </List>
        </Container>
      </Box>
    </>
  )
}

export default WordListAPI
