import React from 'react'
import { Switch, Route } from 'react-router-dom'

import Selectors from './Selectors'
import WordList from './WordList'
import AsyncWordList from './AsyncWordList'
import WordListAPI from './WordListAPI'
import MemberList from './MemberList'
import Location from './Location'
import PostForm from './PostForm'
import PostFormData from './PostFormData'

export default function Routes() {
  return (
    <Switch>
      <Route path="/cypress/selectors" component={Selectors}></Route>
      <Route path="/cypress/async-word-list" component={AsyncWordList}></Route>
      <Route path="/cypress/word-list" component={WordList}></Route>
      <Route path="/cypress/word-list-api" component={WordListAPI}></Route>
      <Route path="/cypress/member-list" component={MemberList}></Route>
      <Route path="/cypress/location" component={Location}></Route>
      <Route path="/cypress/post-form" component={PostForm}></Route>
      <Route path="/cypress/post-form-data" component={PostFormData}></Route>
    </Switch>
  )
}
